.header-content {
  position: relative;
  width: 100%;
  height: 100%; /* адаптивна висота */
  overflow: hidden;
}

/* Фон */
.headerbg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

/* Аватар */
.user-avatar {
  position: absolute;
  top: 50%;
  left: 6.8vw;
  width: 16vw;
  height: 16vw;
  transform: translateY(-50%);
  border-radius: 50%;
  border: 1px solid white;
  object-fit: cover;
  z-index: 2;
}

/* Нікнейм */
.nickname {
  font-family: 'GNF', sans-serif;
  position: absolute;
  top: 33%;
  left: 30vw;
  transform: translateY(-50%);
  color: white;
  font-size: 5vw;
  font-weight: bold;
  margin: 0;
  z-index: 2;
  white-space: nowrap;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}

/* Ресурси */
.coins-row,
.gems-row {
  position: absolute;
  top: 52%;
  display: flex;
  align-items: center;
  gap: 1.2vw;
  z-index: 2;
}

/* Монети праворуч */
.coins-row {
  left: 30vw;
}

/* Геми трохи лівіше */
.gems-row {
  left: 55vw;
}

/* Іконки */
.coin,
.gem {
  width: 6vw;
  height: 6vw;
}

/* Кількість */
.CoinsQuantity,
.GemsQuantity {
  font-size: 4vw;
  font-family: 'GNF', sans-serif;
  color: white;
  margin: 0;
  white-space: nowrap;
}
