/* Обгортка всієї сторінки */
.page-wrapper {
  display: flex;
  flex-direction: column;
}

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
   overflow-x: hidden;
}


/* Основний контент буде займати весь простір */
.main-content {
  flex: 1;
}

.button-row {
  display: flex;
  justify-content: center;  /* Центрує ряд */
  align-items: center;
  gap: 2%;                /* Відступи між кнопками */
  margin-top: 2%;
}

@font-face {
  font-family: 'GNF';
  src: url('./assets/fonts/GNF.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
