.sprite-button {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  /* Зміщення вправо */
  transform: translateX(45%); /* Можеш змінити число на потрібне */
}

.sprite-icon {
  width: 43%;
  height: 43%;
  transition: transform 0.1s ease;
}

.sprite-button {
  outline: none;
  box-shadow: none;
  -webkit-tap-highlight-color: transparent;
}

.sprite-button:focus,
.sprite-button:active {
  outline: none;
  box-shadow: none;
}