.ton-button {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
    transform: translateX(25%); /* Можеш змінити число на потрібне */
}

.ton-icon {
  width: 40%;
  height: 40%;
  transition: transform 0.1s ease;
}


.ton-button {
  outline: none;
  box-shadow: none;
  -webkit-tap-highlight-color: transparent;
}

.ton-button:focus,
.ton-button:active {
  outline: none;
  box-shadow: none;
}